@import "_mixins/mixins";
@import "variables";

/* Custon Style */

// Header
.main-header {
  height: 100vh;
  background-image: url(../img/header.jpg);
  background-size: cover;
  background-position: right bottom;
  background-repeat: no-repeat;
}

.navbar {
  position: relative;
  padding-top: 88px;
  padding-right: 240px / 1600px * 100%;
  padding-left: 240px / 1600px * 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  width: 80px;
  height: 80px;
}

.header-menu {
  display: flex;
  flex-direction: column;
  align-items: center;

    button {
      padding: 0;
      border: none;
      font: inherit;
      color: inherit;
      background-color: transparent;
      outline: none;
    }
}

.menu-button {
	display: block;
	width: 28px;
  height: 28px;
  
    span {
      position: relative;
      display: block;
    }

    span:before {
      content: "";
      position: absolute;
      left: 0;
      top: -9px;
    }

    span:after {
      content: "";
      position: absolute;
      left: 0;
      top: 9px;
    }

    span, span:after, span:before {
      width: 100%;
      height: 4px;
      background-color: $text-color;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 6px;
    }

    &.on span {
      background-color: transparent;
    }
    
    &.on span:before {
      transform: rotate(45deg) translate(6px, 6px);
    }
    
    &.on span:after {
      transform: rotate(-45deg) translate(7px, -6px);
    }
}

.menu-title {
  margin-top: 8px;
  font-size: 12px;
  text-transform: uppercase;
}

.menu-box {
  position: absolute;
  display: none;
  padding: 30px 0;
  top: 180px;
  right: 240px / 1600px * 100%;
  width: 250px;
  background-color: #42b7c8;
  opacity: .8;
  z-index: 1000;
  overflow: auto;
}

.menu-item {
  display: block;
  padding: 10px 0;
  text-align: center;
  font-weight: 300;
  color: $text-color;
  text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
}

.header-content {
  margin-top: 202px;
  margin-right: 240px / 1600px * 100%;
  margin-left: 240px / 1600px * 100%;

    h1 {
      margin: 0;
      font-size: 72px;
      font-weight: 300;
      color: $text-color;
    }

    h1 span {
      display: block;
    }
}

.content-text {
  margin: 0;
  margin-top: 26px;
  font-size: 30px;
  font-weight: 300;

    b {
      font-weight: 700;
    }
}

.content-text-top {
  margin-top: 16px;
}

// About
.about {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 500px;
  background-color: #f3f1ef;
}

.photo-hero {
  grid-column: 1 / 3;
  background-image: url(../img/photo_1.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.text-hero {
  grid-column: 3 / 5;
  padding-top: 112px;
  padding-right: 120px / 800px * 100%;
  padding-left: 60px / 800px * 100%;
  
    h2 {
      margin: 0;
      font-size: 24px;
      font-weight: 700;
      color: #666666;
      text-transform: uppercase;
    }

    p {
      margin: 0;
      margin-top: 42px;
      font-size: 36px;
      font-weight: 300;
      color: #666666;
    }
}

// Gallery
.gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 515px 515px;
  background-color: #3f729b;
}

.photo-1, .photo-2, .photo-3, .photo-4 {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.photo-1 {
  background-image: url(../img/photo_2-1.jpg);
}

.photo-2 {
  background-image: url(../img/photo_2-2.jpg);
}

.photo-3 {
  background-image: url(../img/photo_2-3.jpg);
}

.photo-4 {
  background-image: url(../img/photo_2-4.jpg);
}

.instagram {
  grid-column: span 2;
  padding-top: 126px;
  padding-right: 90px / 800px * 100%;
  padding-left: 212px / 800px * 100%;

    h2 {
      margin: 0;
      font-size: 24px;
      font-weight: 700;
      color: $light-color;
      text-transform: uppercase;
    }

    p {
      margin: 0;
      margin-top: 42px;
      font-size: 36px;
      font-weight: 300;
      color: $light-color;
    }

    .button {
      display: inline-block;
      margin-top: 38px;
      padding: 20px 28px;
      font-size: 18px;
      font-weight: 300;
      text-decoration: none;
      letter-spacing: 3.6px;
      color: $light-color;
      text-decoration: none;
      text-transform: uppercase;
      border: 2px solid $light-color;
      border-radius: 30px;
    }
}

.instagram-photo {
  background-image: url(../img/photo_3.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

// Music
.music {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 900px 520px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #282b2f;
}

.player {
  grid-column: 2 / 5;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 500px 400px;
  background-image: url(../img/photo_dj.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

