// Footer
.main-footer {
  min-height: 600px;
  background-color: #212227;
}

.footer-inner {
  padding-top: 100px;
  padding-bottom: 30px;
  margin-right: 210px / 1600px * 100%;
  margin-left: 210px / 1600px * 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.box-one {
  width: 280px / 1180px * 100%;

