.follow-social {
  margin-top: 30px;
}

.social {
  position: relative;
  display: block;
  margin: 4px 0;
  padding-left: 30px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: $footer-color;
  text-transform: uppercase;
  text-decoration: none;
}

.social-facebook::before,
.social-twitter::before,
.social-instagram::before,
.social-spotify::before {
  position: absolute;
  content: "";
  top: 3px;
  left: 0;
  background-size: cover;
  width: 14px;
  height: 14px;
}

.social-facebook::before {
